* {
  font-family: "Times New Roman", Times, serif;
}

.grid-area {
  margin-top: 40px;
  table {
    width: 300px;
    height: 300px;
    margin: auto;
    user-select: none;
    td {
      width: 10px;
      height: 10px;
      &.selected {
        color: rgb(236, 67, 67);
       
      }
    }
  }
}
